import Table from 'react-bootstrap/Table';
import { useDispatch, useSelector } from 'react-redux';
import { changeName, changeAge } from './../store/userSlice.js'
import {increaseCount, deleteData} from './../store/cartData.js'
import { memo, useMemo, useState } from 'react';

let Child = memo(function(){
  console.log('재렌더링됨')
  return <div>자식임</div>
})


function 반복() {
  return <div></div>
}


function Cart(){
  let result = useMemo(()=>{return 반복()}, [])
  let state = useSelector((state)=> state)
  let dispatch = useDispatch()
  let [count, setCount] = useState(0);

  return(
    <div>

      <Child count={count}></Child>
      <button onClick={()=>{setCount(count+1)}}>+</button>
      <h6>{state.user.name} {state.user.age}의 장바구니</h6>
      <button onClick={()=>{dispatch(changeAge(100))}}> age+ </button>

      <Table striped bordered hover>
        <thead>
          <tr>
            <th>#</th>
            <th>상품명</th>
            <th>수량</th>
            <th>변경하기</th>
            <th>삭제하기</th>
          </tr>
        </thead>
        {
          state.cartData.map((data, idx)=>{
            return (
              <DataRow key={data.id} data={data}/>
            )
          })
        }
      </Table>
    </div>
  )
}

function DataRow({data}){
  let dispatch = useDispatch()

  return(
    <tbody>
      <tr>
        <td>{data.id}</td>
        <td>{data.name}</td>
        <td>{data.count}</td>
        <td>
          <button onClick={() => {
            dispatch(increaseCount(data.id))
          }}>
             + 
          </button>
        </td>
        <td>
          <button onClick={()=>{
            dispatch(deleteData(data.id))
          }}>
            삭제
          </button>
        </td>
      </tr>
    </tbody>
  )
  
}

export default Cart;